


.item-wrapper .list-group-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.item-wrapper button:hover {
    background-color: #ccc;
}

.item-wrapper button:nth-child(1) {
    margin-right: 5px;
}

.item-wrapper .group-item-info {
    cursor: pointer;
}

.marked {
    font-weight: bolder;
}

.line-through {
    text-decoration: line-through;
    color: #4c1f1f;
}